.price {
    &__swiper {
      padding-top: 0;

    }
    &__head {
        color: $footer-background;
        font-size: 14px;
        font-weight: 500;
        opacity: 0.7218424677848816;
        display: none;
     
        @media (min-width: 768px) {
          display: grid;
          margin-left: 20px;
          grid-template-columns: minmax(372px, auto) minmax(96px, auto) minmax(
              220px,
              auto
            );
          justify-content: space-between;
          align-items: center;
        
        }
        @media (min-width: 1120px) {
            margin-left: 320px;
        }
      }
      &__wrapper {        
        @media (min-width: 768px) { 
          border-radius: 6px;
          margin-top: -10px;
          display: flex;
          flex-direction: column;
          background-color: $white;
        }
      }
      &__slide {
        height: 200px;
        display: flex;
       padding: 10px 10px 10px 0;
        flex-direction: column;
        justify-content: space-between;
        color: $footer-background;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: 0.2px;
        border-radius: 6px;
        border: 1px solid $light-gray;
        background: $white;
        
        @media (min-width: 768px) {
          width: auto;
          margin: 0 16px;
          height: 64px;
          padding: 0;
          display: grid;
          grid-template-columns: minmax(372px, auto) minmax(96px, auto) minmax(
              220px,
              auto
            );
          border: none;
          align-items: center;
          border-radius: 0%;
          border-bottom: 1px solid $light-gray;
          &:last-child {
            border-bottom: none;
          }
          @media (min-width: 1120px) {
            grid-template-columns: 360px 137px 317px;
           
          }
        }
      }
      &__button:hover {
        transform: scale(1.1);
      } 
      &__button{
        
        transition: 0.4s;
      } 
      &__order {
        display: flex;
        justify-content: space-between;
        align-items: end;
        @media (min-width: 768px) {
          align-items: center;
        }
      }
      &__title {
        @media (min-width: 1120px) {
          margin-left: 280px;
        }
      }
      &__text {
        color: $footer-background;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        margin-left: 16px;
        margin-top: 10px;
        letter-spacing: 0.2px;
        padding-bottom: 20px;
        @media (min-width: 768px) {
            max-width: 768px;
        }
        @media (min-width: 1120px) {
          
          margin-left: 320px;
        }
      }
      &__link {
        color: $footer-background;
        font-size: 16px;
        font-weight: 500;
        letter-spacing: -0.2px;
        display: flex;
        margin-left: 16px;
        line-height: 10px;
        padding-bottom: 40px;
        font-family: "TT Lakes Bold";
        img {
          position: relative;
          top: 2px;
          margin-left: 2px;
        }
        @media (min-width: 1120px) {
          
          margin-left: 320px;
        }
      }
 
    }