@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";

@import "blocks/price";
@import "blocks/footer";

.container {
  @media (min-width: 540px) {
    padding-left: 16px;
  }
  @media (min-width: 1120px) {
    padding-left: 40px;
  }
}
.header {
  &__open {
    display: flex;
    position: relative;
    z-index: 2;
    align-items: center;
    justify-self: start;
  }

}

.active,
.act,
.active__main {
  transform: rotate(180deg);
}
.hidden,
.hid,
.hidden__text {
  display: none;
}

.burger {
  max-width: 320px;
  height: 100%;
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 10;
  visibility: hidden;
  opacity: 0;
  background-color: $white;

  &.open {
    left: 0;
    visibility: visible;
    opacity: 1;
  }
  &__open {
    border: none;
    background-color: $white;
  }
  &__close {
    border: none;
    background-color: $white;
    margin-right: 10px;
    @media (min-width: 1120px) {
      display: none;
    }
  }
  @media (min-width: 1120px) {
    left: 0;
    visibility: visible;
    opacity: 1;
  }
  &__container {
    position: relative;
    width: 320px;
    height: 100%;
    z-index: 10;
    background-color: $white;
    box-shadow:
      10px 0px 52px 0px rgba(14, 24, 80, 0.2),
      -2px 0px 4px 0px rgba(69, 79, 126, 0.02);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.4s;
    @media (min-width: 1120px) {
      box-shadow: none;
      width: 306px;
    }
  }
  &__wrapper--head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 24px 0 0 23px;
  }
  &__wrapper--logo {
    padding-right: 78px;
  }
  &__wrapper--icon {
    padding-right: 24px;
  }
  &__menu {
    display: block;
    margin-top: 30px;
  }
  &__button {
    display: block;
    font-size: 16px;
    font-family: "TT Lakes";
    color:$footer-background;
    line-height: 26px;
    margin-bottom: 20px;
    margin-left: 24px;
    border: none;
    background-color: $white;
    opacity: 0.9;
    transition: 0.5s;
    &::before {
      content: "";
      width: 4px;
      height: 28px;
      border-radius: 10px;
      position: absolute;
      top: 0px;
      left: -15px;
    }
    &:hover {
      background-image: linear-gradient(#c292c4, #ce4e98);
      opacity: 1;
      transform: scale(1.1);
      &:before {
        background-color: $highlight;
      }
    }
  }
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $white;
    opacity: 0.96;
    @media (min-width: 1120px) {
      display: none;
    }
  }
  &__footer {
    margin-left: 24px;
    margin-top: 150px;
    @media (max-height: 700px) {
      margin-top: 40px;
      margin-bottom: 150px;
    }
  }
}

.burger__footer--icon {
  margin-bottom: 16px;
}
.burger__footer--icon2,
.burger__footer--icon3 {
  margin-left: 16px;
}

.burger__footer--icon1:hover,
.burger__footer--icon2:hover,
.burger__footer--icon3:hover {
  transform: scale(1.2);
}
.footer__mail {
  display: block;
  margin-bottom: 12px;
  text-decoration: none;
  font-size: 16px;
  font-family: "TT Lakes Regular";
  line-height: 24px;
  color:$footer-background;
}
.footer__phone {
  text-decoration: none;
  font-size: 24px;
  font-family: "TT Lakes Bold";
  line-height: 32px;
  color:$footer-background;
  letter-spacing: -0.6 px;
}

.lang__button {
  margin-top: 30px;
  border: none;
  background-color: $white;
  transition: 0.9s;
}

.lang__button:focus {
  background-image: linear-gradient(#511b53, #c430b0);
  transform: rotate(360deg);
}
/*====================================*/

.header {
  background-color: $white;
}

.header__inner {
  min-height: 88px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}

.header__menu {
  width: 152px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__tools {
  width: 118px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 10px;
}

.header__partition {
  width: 2px;
  height: 32px;
  background: $light-gray;
}
.menu__hiden {
  display: none;
}

/*=================================*/
.main {
  background-color: $background;
  padding-top: 24px;

  @media (min-width: 1120px) {
    margin-left: 285px;
  }
  &__content {
    margin-top: 20px;
    margin-left: 16px;
  }
  &__servis {
    padding-top: 7px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    font-size: 28px;
    font-family: "TT Lakes Bold";
    @media (min-width: 1120px) {
   
    }
  }
  &__driver {
    width: 4px;
    height: 40px;
    background: $highlight;
    position: absolute;
    border-radius: 0 4px 4px 0;
    @media (min-width: 1120px) {
      margin-left: 26px;
      margin-top: 7px;
    }
  }
}



.main__items {
  display: none;
}

.container__card--swipe {
  display: flex;
}
/* Скролkkkkkkk*/

.scrool {
  display: flex;
  overflow: auto;
  margin-top: 20px;
}
.scrool::-webkit-scrollbar {
  display: none;
}
.scrool__button {
  font-family: "TT Lakes Medium";
  background-color: $background;
  font-size: 16px;
  line-height: 24px;
  width: 240px;
  color: $menu-text-color;
  border: none;
  white-space: nowrap;
}
.scrool__button:hover {
  border: solid 2px #b8ffec;
  border-radius: 15px;
  background-color: $white;
}

/*==================================================*/

.main__text {
  font-size: 14px;
  font-family: "TT Lakes Regular";
  font-weight: 400;
  line-height: 18px;
}
.main__text-768 {
  display: none;
}
.main__link {
  display: flex;
  align-items: center;
  margin-top: 15px;
}
.main__link-title {
  font-size: 16px;
  font-family: "TT Lakes Medium";
  line-height: 18px;
}

.main__img {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 34px;
}
.main__link-title,
.btn__repair {
  border: none;
}
.btn__text {
  display: flex;
}
