Свайпер
/*===========================================*/
.brands {
  
}
.brands__title,
.repair__title,
.price__title {
  border-top: 1px solid #d9fff5;
  padding: 16px 4%;
  line-height: 24px;
  font-family: "TT Lakes Regular";
  margin-bottom: 25px;
  background: linear-gradient(90deg, $background 0%, $white 100%);
  color: $menu-text-color;
  text-transform: uppercase;
}
.swiper {
  margin: 0 0px;
  padding: 24px 16px 0 16px;
  padding-bottom: 46px;
  background: #f8f5f5;
  @media (min-width: 768px) {
    margin: 0;
    padding-bottom: 20px;
  }
}

.brands__wrapper {
  @media (min-width: 768px) {
    width: 100%;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, 224px);
    gap: 16px 24px;
  }
  @media (min-width: 1120px) {
    grid-template-columns: repeat(auto-fit, 240px);
    gap: 16px 24px;
  }
}
.brands__container,
.brands__container--1,
.brands__container--2,
.brands__container--3,
.brands__container--4,
.brands__container--5 {
  height: 72px;
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 6px;
  border: 1px solid $light-gray;
  background: $white;
  @media (min-width: 1120px) {
  }
}
.swiper-pagination {
  background-color: transparent;
  width: 15px;
  height: 15px;
  opacity: 1;
  border-radius: 0;
  padding-bottom: 16px;

  @media (min-width: 768px) {
    display: none;
  }
}
.swiper-pagination-bullet {
  background: #b5b6bc;
  width: 12px;
  height: 12px;
}

.container__card--hidden {
  height: 72px;
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 6px;
  border: 1px solid $light-gray;
  background: $white;
}
.swiper-wrapper-hidden {
  width: 100%;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 224px);
  gap: 16px 24px;
  margin-top: 20px;
  margin-top: -15px;

  @media (max-width: 767px) {
    display: none;
  }
  @media (min-width: 1000px) {
    margin-top: 0;
  }
  @media (min-width: 1120px) {
    margin-top: 16px;
    grid-template-columns: repeat(3, 240px);
    margin-top: -15px;
  }
  @media (min-width: 1314px) {
    margin-top: 0px;
    grid-template-columns: repeat(4, 240px);
  }
  @media (min-width: 1578px) {
    grid-template-columns: repeat(5, 240px);
  }
  @media (min-width: 1678px) {
    grid-template-columns: repeat(5, 240px);
    margin-top: 16px;
    margin-left: -131px;
  }
}

.brands__container--1,
.brands__container--3,
.brands__container--4,
.brands__container--5,
.brands__container--2 {
  @media (min-width: 768px) {
    display: none;
  }
}
.brands__container--1,
.brands__container--2 {
  @media (min-width: 1000px) {
    height: 72px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 6px;
    border: 1px solid $light-gray;
    background: $white;
  }
  @media (min-width: 1120px) {
    display: none;
  }
  @media (min-width: 1314px) {
    display: block;
    height: 72px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 6px;
    border: 1px solid $light-gray;
    background: $white;
  }
}
.brands__container--4,
.brands__container--3 {
  @media (min-width: 1578px) {
    display: block;
    height: 72px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 6px;
    border: 1px solid $light-gray;
    background: $white;
  }
}
.brands__container--5 {
  @media (min-width: 1678px) {
    display: block;
    height: 72px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 6px;
    border: 1px solid $light-gray;
    background: $white;
  }
}
.brands,
.repair {
  @media (min-width: 1120px) {
    margin-left: 295px;
  }
}

