  }
}
.repair__container--2 {
  @media (min-width: 1578px) {
    display: block;
  }
}
.repair__container--3 {
  @media (min-width: 1714px) {
    display: block;
  }
}
.repair__container--card {
  height: 160px;
  font-family: "TT Lakes Regular";
  font-size: 16px;
