  background: $white;
}
.repair__hidden {
  width: 100%;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 224px);
  gap: 16px 24px;
  margin-top: 20px;
  margin-top: -15px;

  @media (max-width: 767px) {
    display: none;
  }
  @media (min-width: 1000px) {
    margin-top: 0;
  }
  @media (min-width: 1120px) {
    grid-template-columns: repeat(auto-fit, 240px);
    gap: 16px 24px;
  }
}
