/////////////////////////////////////////////////////////////////////////////
.call__open {
  border: none;
  background-color: $white;
}
.call__close,
.contact__close {
  border: none;
  background-color: $white;
  margin-left: -230px;
  @media (min-width: 1120px) {
    margin-left: -530px;
  }
}

.contact {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: end;
  z-index: 15;
  visibility: hidden;
  opacity: 0.94;
  transition: all 0.4s;
  &.open-contact {
    visibility: visible;
    opacity: 1;
    & .contact__service {
      transform: scale(1);
    }
  }
  &__service {
    top: 0;
    width: 320px;
    height: 100%;
    z-index: 15;
    transform: scale(0);
    transition: transform 0.4s;
    background-color: $white;
    padding: 24px 16px;
    box-shadow:
      16px 0px 52px 0px rgba(14, 24, 80, 0.2),
      -2px 0px 4px 0px rgba(69, 79, 126, 0.02);
    display: flex;
    flex-direction: column;
    :last-child {
      align-self: flex-end;
    }
    @media (min-width: 1120px) {
      width: 440px;
    }
  }
  &__open,
  &__close {
    border: none;
    background-color: $white;
  }
}

.burger__driver-close {
