  .header__menu {
    width: 180px;
  }

  .header__highlight {
    width: 2px;
    height: 32px;
    background: $light-gray;
  }
  .header__tools {
    width: 290px;
  }
  .header__hiden {
    display: block;
  }
  .scrool {
    margin: 32px auto;
  }

  .main__wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
  }
  .main__text {
    width: 312px;
  }
  .menu__hiden {
    display: block;
  }
  .main__text-768 {
    display: block;
  }
  .main__text-1120 {
    display: none;
  }
  .main__img {
    width: 360px;
  }
  .main__link {
    margin: 10px 0;
  }
