  }
  .main__items {
    display: block;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main__item-title {
    margin-right: 10px;
    margin-left: 32px;
  }
  .scrool {
    margin: 10px auto 24px;
  }
  .main__wrapper {
    padding-bottom: 42px;
  }
  .main__text {
    width: 490px;
  }
  .main__img {
    width: 540px;
  }
  .main__text-1120 {
    display: inline;
  }
  .main__content {
    padding-left: 30px;
  }
}
